<template>
     <section class="search">
         <!-- <header class="header">
              <div class="header_search">
             </div>
             <div class="header_title">
                 <span class="header_title_text">搜索</span>
             </div>
         </header> -->
         <HeaderTop title="搜索"></HeaderTop>
         <form class="search_from" action="#">
                <input type="search" name="search_submit" placeholder="请输入商家或美食名称" class="search_input">
                <input type="submit" class="search_submit">
         </form>
     </section>
</template>

<script>
import HeaderTop from '../../components/HeaderTop/HeaderTop.vue'
export default {
    components: { 
        HeaderTop 
    }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "../../common/stylus/mixins.styl";

.search
    width 100%
    height 100%
    .header
        background-color #02a774
        position fixed
        z-index 100
        left 0
        top 0
        width 100%
        height 45px
        .header_search
            position absolute
            left 15px
            top 50%
            tranform translateY(-50%)
            width 10%
            height 50%
            background red
            .iconfont
                font-size 22px
                color #fff
        .header_title
            position absolute
            top 50%
            left 50%
            transform translate(-50%,-50%)
            width 30%
            color #fff
            font-size 22px
            text-align center
    .search_from
        clearFix()
        margin-top 45px
        background-color #fff
        padding 12px 8px
        text-align center
        input 
            height 35px
            padding 0 4px
            border-radius 2px
            font-weight bold
            outline none
            &.search_input
                float left
                width 79%
                border 4px solid #f2f2f2
                font-size 14px
                color #333
                background-color #f2f2f2
            &.search_submit
                float right
                width 18%
                border 4px solid #f2f2f2
                font-size 16px
                color #fff
                background-color #02a774    

</style>


